import { Component, OnInit,ViewChild } from '@angular/core';
import { Platform, IonSlides } from '@ionic/angular';
import { ModalController } from '@ionic/angular';
import { CityDetails } from 'src/app/Models/city-details';
import { RegisterService } from 'src/app/Services/register.service';
import { TouristService } from 'src/app/Services/tourist.service';
@Component({
  selector: 'app-city-details-modal',
  templateUrl: './city-details-modal.page.html',
  styleUrls: ['./city-details-modal.page.scss'],
})
export class CityDetailsModalPage implements OnInit {
  // @Input() name: string;
  @ViewChild('slideWithNav2', { static: false }) slideWithNav2: IonSlides;

  sliderTwo: any;
  slideOptsTwo = {
    initialSlide: 1,
    slidesPerView: 2,
    loop: true,
    centeredSlides: true,
    spaceBetween: 5
  };
  albumDisplay:boolean = false;
  id:any;
  cityData:CityDetails;

  constructor(
    private modalCtr: ModalController,
    public registerService:RegisterService,
    private touristService:TouristService,
  ) { 
    this.cityData = new CityDetails();

  }
  

  ngOnInit() {
  }
  ionViewWillEnter(){
    this.touristService.GetCityDetails(this.id).then(res=>{
      this.cityData = JSON.parse(res.data)
    })
    // console.log(this.id,'asdas')
  }
  async close() {
    const closeModal: string = "Modal Closed";
    await this.modalCtr.dismiss(closeModal);
  }
  getAlbumList(){
    this.albumDisplay = true
  }

}
